export function formatTime(fullTime, local = []) {
  const format = { hour: '2-digit', minute:'2-digit' }
  return new Date(fullTime).toLocaleTimeString(local, format);
}

export function groupBy(list, callback) {
  const obj = {}
  list.forEach(item => {
    const key = callback(item)
    if (!obj[key]) {
      obj[key] = []
    }
    obj[key].push(item)
  })

  return obj
}

export async function getLatestSwBuild() {
  try {
    const url = './meta.js'
    const noCache = { cache: 'no-store' }
    const res = await (await fetch(url, noCache)).text()
    const hash = res.match(/hash: '(.*?)'/)?.[1] || ''
    const date = res.match(/date: '(.*?)'/)?.[1] || ''
    return { date, hash}
  }
  catch {
    console.warn('Not able to check sw')
  }
  return null
}