import { CSSProperties, ReactNode, useEffect } from 'react'
import classNames from 'classnames'

import { CloseButton } from '..'

interface Props {
  children?: ReactNode
  id?: string
  style?: CSSProperties
  className?: string
  onClose?: Function
  center?: boolean
  hidden?: boolean
}

const Page = (props: Props) => {
  const { id, style = {}, className, onClose, center, hidden, children } = props
  const cls = classNames('md-page ',  className)

  const closeButton = onClose && <CloseButton id="page-close" onClick={onClose} />

  if (center) {
    style.justifyContent = 'space-between'
  }

  useEffect(() => {
    const keyUp = (e: any) => {
      const { key } = e
      if (key === 'Escape' && onClose) {
        onClose()
      }
    }
    document.addEventListener('keyup', keyUp)
    return () => document.removeEventListener('keyup', keyUp)
  }, [onClose])

  return (
    <div id={id} className={cls} style={style} aria-hidden={hidden}>
      {children}
      {closeButton}
    </div>
  )
}

export default Page