import { Text, VList as List, HList } from '../../momentum'

const ShareTrayView = (props) => {
  const { items } = props
  const empty = !items.length && <Text>No presentation sources found.</Text>

  const style = {}
  if (items.length > 3) {
    style.justifyContent = 'flex-start'
  }

  return (
    <List>
      <Text type="title">Share Screen</Text>
      <div style={{ overflowX: 'auto', maxWidth: '100%' }}>
        <HList className="share-tray-list" style={style}>{items}</HList>
        {empty}
      </div>
    </List>
  )
}

export default ShareTrayView
