import { VList, Text, Icon, ButtonPill, Avatar } from '../../momentum'


function toParagraphs(text) {
  return text
    .split('\n')
    .map((p, n) => <p key={n}>{p}</p>)
}

const ShareTrayItem = (props) => {
  const { name, info, icon, logo, onSelect, onShare, onStop, onView, onHide, selected } = props

  const cls = 'share-tray-item' + (selected ? ' selected' : '')
  const btn = { width: '20rem' }

  return (
    <VList className={cls} onClick={onSelect}>
      <Avatar size={110} backgroundColor={logo ? 'transparent' : null}>
        <Icon name={icon} className={logo} size={48} />
      </Avatar>
      <Text>{name}</Text>
      <Text type="message" size={18}>{toParagraphs(info)}</Text>

      <div className="md-grow" />
      {/* buttons will be hidden by css if item is not selected */}
      {onShare && <ButtonPill style={btn} color="join" onClick={onShare}>Share in call</ButtonPill>}
      {onStop && <ButtonPill style={btn} color="share" onClick={onStop}>Stop sharing</ButtonPill>}
      {onView && <ButtonPill style={btn} onClick={onView}>View</ButtonPill>}
      {onHide && <ButtonPill style={btn} onClick={onHide}>Hide</ButtonPill>}
    </VList>
  )
}

export default ShareTrayItem
