import { ReactNode, CSSProperties, createElement } from 'react'
import classNames from 'classnames'

// how far finger can move inside button while pressing, and still be considered a click
// const clickToleranse = 50

export interface Props {
  id?: string
  className?: string
  style?: CSSProperties
  children?: ReactNode
  tag?: string
  title?: string
  onClick?: Function
  onPointerDown?: Function
  onPointerUp?: Function
  onPointerOut?: Function
  disabled?: boolean
  shorten?: boolean
  type?: 'primary' | 'secondary' | 'ghost' | undefined
  /** join, share or cancel */
  color?: string
}

const ButtonBase = (props: Props) => {
  const {
    id,
    tag = 'button',
    children,
    onClick,
    onPointerDown,
    onPointerUp,
    onPointerOut,
    color,
    type,
    style,
    title,
    className,
    shorten,
    disabled,
  } = props

  // on touch devices (ipad etc) we need to allow user to move finger a few pixels when clicking
  // by default, browsers are very picky and doesnt fire event if you do
  // const [touchDownPos, setTouchDownPos] = useState({ x: 0, y: 0 })
  // const [hasClicked, setHasClicked] = useState(false)

  // const onTouchStart = (e: any) => {
  //   const { clientX: x, clientY: y } = e.touches[0]
  //   setTouchDownPos({ x, y })
  //   setHasClicked(false)
  // }

  // TODO: may want to consider multitouches
  // const onTouchEnd = (e: any) => {
  //   const { clientX: x, clientY: y } = e.changedTouches[0]
  //   const dx = x - touchDownPos.x
  //   const dy = y - touchDownPos.y
  //   const dist2 = (dx * dx) + (dy * dy)

  //   if (dist2 < clickToleranse ** 2 && onClick) {
  //     onClick()
  //     setHasClicked(true)
  //   }
  // }
  // const click = (e: any) => {
  //   if (!hasClicked && onClick) {
  //     onClick()
  //   }
  //   e.stopImmediatePropagation()
  //   setHasClicked(false)
  // }

  const cls = classNames('md-button', { 'md-one-line': shorten }, className)
  const onContextMenu = (e: any) => {
    if (onPointerDown) {
      e.preventDefault()
    }
  }

  const prop = {
    id,
    onClick,
    onPointerDown,
    onPointerUp,
    onPointerOut,
    // onTouchStart,
    // onTouchEnd,
    className: cls,
    style,
    title,
    disabled,
    'data-color': color,
    'data-type': type,
    onContextMenu,
  }

  return createElement(tag, prop, children)
}

export default ButtonBase