import { VList, ButtonRow, Icon, Text } from '../../momentum'

const Row = ({ name, value }) => {
  return (
    <VList hAlign='left' className="md-wide" gap={8}>
      <Text type="message">{name}</Text>
      <Text>{value}</Text>
    </VList>
  )
}

const Disclaimer = () => (
  <Text left multiLine>This controller is running a minimal version of the user interface.
    For access to all features and advanced configurations, use the Cisco Navigator or
    the user interface on the main device.
  </Text>
)


const DeviceInfo = (props) => {
  const { actions } = props
  const { disconnect } = actions

  const unpair = () => {
    const ok = window.confirm('This will un-pair this controller from the Cisco device, and delete any data stored on the device. Continue?')
    if (ok) {
      disconnect(true)
    }
  }

  const reload = () => window.location.reload()
  const { hash, date } = window.build || {}
  const time = new Date(date).toGMTString()
  const version = date ? `${time} (${hash})` : 'Unknown'

  // const showLogViewer = () => actions.showDialog('logViewer')

  return (
    <VList gap={48} style={{ maxWidth: 600 }}>
      <Text type="title">About this controller</Text>
      <VList hAlign="left" gap={24} className="md-wide">
        <Row name="Software Version" value={version} />
        <Row name="Info" value={<Disclaimer />} />
        <VList gap={1} className="md-wide">
          <ButtonRow onClick={reload}>
            <Icon name="refresh" weight="bold" />
            <Text left className="md-grow">Reload app</Text>
          </ButtonRow>
          {/* <ButtonRow onClick={showLogViewer}>
            <Icon name="file-text" weight="bold" />
            <Text left className="md-grow">Logs</Text>
          </ButtonRow> */}
          <ButtonRow onClick={unpair} color="cancel">
            <Icon name="unlink" weight="bold" />
            <Text left className="md-grow">Unpair from device</Text>
          </ButtonRow>
        </VList> 
      </VList>
    </VList>
  )
}

export default DeviceInfo
