import { Avatar, ButtonRow, Icon, VList as List, Text } from '../../momentum'

const AboutDevice = (props) => {
  const { appState, actions } = props
  const { status, commands } = appState.device
  const { displayName, number } = status
  const showAbout = () => actions.showDialog('deviceInfo')
  const showController = () => actions.showDialog('controllerInfo')
  const standby = () => commands.setStandby('standby')

  return (
    <List gap={36}>
      <Avatar size={80} name={displayName}>
        <Icon name="location" size={36} />
      </Avatar>
      <List gap={1}>
        <Text type="title" size={20}>{displayName}</Text>
        <Text type="message" size={18}>{number}</Text>
        {/* <Text type="message" size={18}>{creds.host}</Text> */}
      </List>
      <List className="md-wide" hAlign='left' gap={1}>
        <ButtonRow onClick={showAbout}>
          <Icon name="webex-board" weight="bold" />
          <Text left className="md-grow">About Video Device</Text>
          <Icon name="arrow-right" />
        </ButtonRow>
        <ButtonRow onClick={showController}>
          <Icon name="tablet" weight="bold" />
          <Text left className="md-grow">About Controller</Text>
          <Icon name="arrow-right" />
        </ButtonRow>
        <ButtonRow onClick={standby}>
          <Icon name="power" weight="bold" />
          <Text left className="md-grow">Standby</Text>
        </ButtonRow>
      </List>
    </List>
  )
}

export default AboutDevice
