
import { ReactNode } from 'react'

import ButtonSimple from './ButtonSimple'

interface Props {
  children?: ReactNode
  onClick?: Function
  color?: string
}

const ButtonRow = (props: Props) => {
  const { children, onClick, color } = props
  return (
    <ButtonSimple
      className="md-button-row"
      onClick={onClick}
      type="secondary"
      color={color}
    >
        {children}
      </ButtonSimple>
  )
}

export default ButtonRow