import classNames from 'classnames'

import { default as ButtonSimple, Props as BaseProps } from './ButtonSimple'

export interface Props extends BaseProps {
  size?: 110 | 80 | 60 | 48 | 40
  isRound?: boolean
}

const ButtonPill = (props: Props) => {
  const {
    id,
    children,
    className,
    disabled,
    size,
    isRound,
    color = '',
    type = 'primary', // primary, secondary, ghost
    title,
    onClick,
    onPointerDown,
    onPointerUp,
    onPointerOut,
  } = props

  const cls = classNames('md-button-pill', isRound && 'md-button-circle', className)

  let style = Object.assign({}, props.style)
  if (size) {
    style.height = (size / 10) + 'rem'
  }

  return (
    <ButtonSimple
      id={id}
      className={cls}
      disabled={disabled}
      style={style}
      color={color}
      type={type}
      onClick={onClick}
      onPointerDown={onPointerDown}
      onPointerUp={onPointerUp}
      onPointerOut={onPointerOut}
      title={title}
    >{children}</ButtonSimple>
  )
}

export default ButtonPill
