// TODO
// No components in the momentum library
// should use xapi details. transform bookings list

import { Text, HList, MeetingInfo, Icon } from '..'

const MeetingWidget = (props) => {
  const { bookings = [], join, locale, isInCall } = props

  const max = 3
  const limited = bookings.slice(0, max)
  const list = limited.map(booking => {
    const { Id, Title, MeetingPlatform, Organizer, Time, DialInfo } = booking
    const number = DialInfo?.Calls?.Call?.[0]?.Number

    return (
      <MeetingInfo
        key={Id}
        title={Title}
        organizer={Organizer?.FirstName}
        startTime={Time.StartTime}
        locale={locale}
        showJoin={!!number}
        number={number}
        canJoin={!isInCall}
        startBuffer={Time.StartBuffer}
        endTime={Time.EndTime}
        platform={MeetingPlatform}
        onJoin={() => join(booking)}
      />
    )
  })

  const additional = bookings.length - max
  const info = additional > 0
    ? `${additional} more ongoing meeting(s)`
    : 'No more ongoing meetings'

    return (
    <section className="md-meeting-widget" aria-label="One button to push meetings">
      {list}
      <HList>
        <Icon name="webex-meetings" weight="filled"></Icon>
        <Text left>{info}</Text>
      </HList>
    </section>
  )
}

export default MeetingWidget