import { VList as List, Icon, Text } from '../../momentum'

// might be more here, dont really know the full valuespace
const iconNames = {
  stack: 'video-layout-stack',
  sidebyside: 'layout-side-by-side-vertical',
  prominent: 'video-layout-prominent',
  floating: 'video-layout-presenter-dominant',
  focus: 'video-layout-share-dominant',
  grid: 'video-layout-equal',
  equal: 'video-layout-equal',
}

const Layouts = (props) => {
  const { status, commands } = props.appState.device
  const { currentLayout, layouts } = status.call

  const buttons = layouts.map(name => {
    const selected = name === currentLayout
    const cls = selected ? 'selected' : ''
    const shortName = name.toLowerCase().replaceAll(' ', '')
    const icon = iconNames[shortName] || 'video-layout-single'

    return (
      <button
        className={cls}
        onClick={() => commands.setLayout(name)}
        key={name}
      >
        <Icon name={icon} size={40} weight="bold" /> {name}
      </button>
    )
  })

  return (
    <List gap={32}>
      <Text class="Title">Layouts</Text>
      <div className="layout-list">
        {buttons}
      </div>
    </List>
  )
}

export default Layouts