// One Button To Clap

// TODO
// - Clear indication when sending reaction

import { useRef } from 'react'
import { HList, ButtonSimple, Icon } from '../../momentum'

interface ButtonProps {
  name: string
  onReact: Function
  emoji: string
}
const reactions = [
  { name: 'Clap', emoji: '👏' },
  { name: 'Celebrate', emoji: '🎉' },
  { name: 'Haha', emoji: '😃' },
  { name: 'Heart', emoji: '🩷' },
  { name: 'ThumbUp', emoji: '👍' },
  { name: 'Fire', emoji: '🔥' },
  { name: 'Wow', emoji: '😮' },
  { name: 'Prayer', emoji: '🤲' },
]
  // Prayer

const ReactionButton = (props: ButtonProps) => {
  const { name, onReact, emoji } = props
  const ref = useRef<HTMLInputElement>(null)

  const react = () => {
    onReact(name)
    if (ref.current) {
      ref.current.classList.remove('show')
      // eslint-disable-next-line
      const tmp = ref.current.offsetWidth // necessary to re-trigger animation
      ref.current.classList.add('show')
    }
  }

  return (
    <div className="reaction-button">
      <div ref={ref} className="reaction-ghost">{emoji}</div>
      <button onClick={react}>{emoji}</button>
    </div>
  )
}

interface Props {
  onReact: Function
  onClose: Function
}

const Reactions = (props: Props) => {
  const { onReact, onClose } = props
  const buttons = reactions.map(r => (
    <ReactionButton key={r.name} onReact={onReact} name={r.name} emoji={r.emoji} />
  ))

  return (
    <HList className="reactions">
      {buttons}
      <ButtonSimple onClick={onClose}>
        <Icon name="cancel" />
      </ButtonSimple>
    </HList>
  )
}

export default Reactions
