import { useState } from 'react'

import {
  BottomBar, ButtonPill, Icon, HList, VList as List, Page, Spinner, Text, TextInput, TopBar
} from '../momentum'

const ConnectionDialog = (props) => {
  const { appState, actions } = props
  const { creds, error, connection } = appState.device
  const [host, setHost] = useState(creds.host)
  const [username, setUsername] = useState(creds.username)
  const [password, setPassword] = useState(creds.password)
  const [showTips, setShowTips] = useState(false)

  const connecting = connection === 'connecting'
  const lostConnection = connection === 'lostconnection'
  const valid = host.length && username.length

  const connect = () => {
    if (!valid) return
    actions.connect({ host, username, password })
  }

  const disconnect = () => actions.disconnect(false)

  if (lostConnection) {
    return (
      <Page className="start">
        <TopBar>
          <Text type="title">Connection paused</Text>
        </TopBar>
        <List>
          <Text>Reconnecting...</Text>
          <ButtonPill id="connect-disconnect" onClick={disconnect}>Cancel</ButtonPill>
        </List>
      </Page>
    )
  }

  const back = () => setShowTips(false)

  if (showTips) {
    const url = host.startsWith('http') ? host : `https://${host.trim()}`
    const hint = 'Visit the device\'s web interface to verify the above.'
    const visit = host
      ? <a href={url} onClick={back} target="_blank" rel="noreferrer">{hint}</a>
      : hint

    return (
      <Page className="start">
        <TopBar>
          <Text type="title">Connect to a Cisco device</Text>
        </TopBar>
        <List>
        If you are unable to connect, please make sure that:
        <ul>
          <li>Your device is on the same network as the Cisco device</li>
          <li>You have the correct host, username and password</li>
          <li>You have the accepted the certificate on the device's web interface.</li>
          <p/>{visit}
        </ul>
        <ButtonPill id="connect-hide-tips"  type="secondary" onClick={back}>Back</ButtonPill>
        </List>
      </Page>
    )
  }

  const top = connecting ? (
    <HList>
      <Spinner />
      <Text type="title">Connecting...</Text>
    </HList>
  ) : <Text type="title">Connect to a Cisco device</Text>

  const resetError = actions.resetConnectionError

  const showTipsAndClearError = () => {
    setShowTips(true)
    resetError()
  }

  const errorMessage = error && (
    <Text type="error">That didn't work.<p/>
      <span onClick={showTipsAndClearError} className="link">See tips for help</span>
    </Text>
  )

  return (
    <Page className="start">
      <TopBar>{top}</TopBar>
      <List className="md-grow" gap={24}>
        <Text type="message">Log in with the admin user linked to the device.</Text>
        <List>
          <TextInput
            id="connect-host"
            placeholder="Host"
            value={host}
            autoCapitalize="none"
            disabled={connecting}
            onChange={v => { setHost(v); resetError() }}
            onSubmit={connect}
          />
          <TextInput
            id="connect-username"
            placeholder="Username"
            value={username}
            autoCapitalize="none"
            disabled={connecting}
            onChange={v => { setUsername(v); resetError() }}
            onSubmit={connect}
          />
          <TextInput
            id="connect-password"
            type="password"
            value={password}
            placeholder="***"
            disabled={connecting}
            onSubmit={connect}
            onChange={v => { setPassword(v); resetError() }}
          />
        </List>
        {errorMessage}
        {connecting
        ? <ButtonPill id="connect-cancel" onClick={disconnect} type="primary">Cancel</ButtonPill>
        : <ButtonPill id="connect-submit" onClick={connect} disabled={!valid} type="primary">Connect</ButtonPill>
        }

      </List>
      <BottomBar>
        <ButtonPill
          id="connect-show-tips"
          type="ghost"
          onClick={() => showTipsAndClearError()}
        >
          <Icon name="help-circle" weight="bold" />
          How to connect?</ButtonPill>
      </BottomBar>
    </Page>
  )
}

export default ConnectionDialog
