import { useEffect } from 'react'

import '../../assets/styles/dialog.css'
import { CloseButton } from '../../momentum'

const Dialog = (props) => {
  const { close, children, pos } = props
  const clickOutside = e => {
    if (e.target.classList.contains('fade')) {
      close()
    }
  }

  useEffect(() => {
    const keyUp = ({ key }) => {
      if (key === 'Escape' && close) {
        close()
      }
    }
    document.addEventListener('keyup', keyUp)
    return () => document.removeEventListener('keyup', keyUp)
  }, [close])

  const style = pos || null

  return (
    <div className="fade" onClick={clickOutside}>
      <div className="dialog" role="dialog" style={style}>
        <CloseButton onClick={close} id="dialog-close" />
        {children}
      </div>
    </div>
  )
}

export default Dialog
