import { useState } from 'react'

import { ButtonApp, ButtonPill, Icon, VList, HList } from '../../momentum'
import Reactions from './Reactions'

const CallControls = (props: any) => {
  const { commands, status, actions, capabilities } = props
  const [showReactions, setShowReactions] = useState(false)

  const leave = () => commands.endCall()

  const { muted } = status.audio
  const showDTMF = () => actions.showDialog('dtmf')
  const showCameraOptions = () => actions.showDialog('cameraControls')
  const showShare = () => actions.showDialog('shareTray')
  const showLayouts = () => actions.showDialog('layouts')

  const hasLayouts = status.call.layouts?.length > 1

  const mainButtons = []
  mainButtons.push(muted ? {
    id: 'callcontrols-unmute',
    name: 'Unmute',
    desc: 'You are muted, tap to unmute',
    icon: 'microphone-muted',
    iconColor: '#db1f2e',
    color: '#333',
    onClick: () => commands.setMute(false),
  }
  : {
    id: 'callcontrols-mute',
    name: 'Mute',
    desc: 'You are not muted, tap to mute',
    icon: 'microphone',
    color: 'transparent',
    onClick: () => commands.setMute(true),
  })

  mainButtons.push(status.video.muted ? {
    id: 'callcontrols-video-start',
    name: 'Start video',
    desc: 'Video is off, tap to start',
    icon: 'camera-muted',
    iconColor: '#db1f2e',
    color: '#333',
    onClick: () => commands.setVideoMute(false),
  } : {
    id: 'callcontrols-video-stop',
    name: 'Stop Video',
    desc: 'Video is on, tap to stop',
    icon: 'camera',
    color: 'transparent',
    onClick: () => commands.setVideoMute(true),
  })

  mainButtons.push({
    id: 'callcontrols-share',
    name: 'Share',
    desc: 'Share screen',
    icon: 'share-screen',
    color: 'transparent',
    onClick: showShare,
  })

  if (capabilities.reactions) {
    mainButtons.push({
    })
  }

  if (capabilities.keypad) {
    mainButtons.push({
      id: 'callcontrols-keypad-show',
      name: 'Keypad',
      icon: 'dialpad',
      color: 'transparent',
      onClick: showDTMF,
    })
  }

  mainButtons.push({
    id: 'callcontrols-meeting-leave',
    name: 'Leave meeting',
    icon: 'cancel',
    color: '#db1f2e',
    onClick: leave,
  })

  const secondRow = []

  if (hasLayouts) {
    secondRow.push({
      id: 'callcontrols-layouts-show',
      name: 'Layouts',
      desc: 'Video layouts',
      icon: 'video-layout',
      color: 'transparent',
      onClick: showLayouts,
    })
  }

  secondRow.push({
    id: 'callcontrols-reactions-show',
    name: 'Reactions',
    icon: 'reactions',
    color: showReactions ? '#333' : 'transparent',
    onClick: () => setShowReactions(!showReactions),
  })
  
  secondRow.push({
    id: 'callcontrols-camera-settings-show',
    name: 'Camera',
    icon: 'adjust-video',
    color: 'transparent',
    onClick: showCameraOptions,
  })


  const list = mainButtons.map(btn => {
    const { id, icon, name, desc, color, iconColor, onClick } = btn
    return (
      <ButtonApp
        key={id}
        id={id}
        icon={icon}
        name={name || ''}
        description={desc}
        type={icon === 'cancel' ? undefined : 'secondary'}
        color={color}
        iconColor={iconColor}
        weight='regular'
        onClick={onClick}
      />
    )}
  )

  const secondary = secondRow.map(button => {
    const { id, icon, name, onClick } = button
    return (
      <ButtonPill key={id} type="secondary" onClick={onClick}>
        <Icon name={icon} />
        {name}
      </ButtonPill>
    )
  })

  return (
    <VList gap={72}>
      <HList className="call-controls" hAlign='left' wrap vAlign="top">
        {list}
        {showReactions &&
          <Reactions
            onReact={(r: string) => commands.sendReaction(r)}
            onClose={() => setShowReactions(false)}
          />
        }
      </HList>
      <HList wrap hAlign="center">{secondary}</HList>
    </VList>
  )
}

export default CallControls