/**
 * Pure data handler class.
 *
 * Maps from xapi data structure to a ui friendly share tray model, that is fed to ShareTrayView
 */

import { useState } from 'react'

import ShareTrayView from "./ShareTrayView"
import ShareTrayItem from "./ShareTrayItem"

function showInShareTray(source) {
  const { Visibility, Connected, SignalState } = source

  if (Visibility === 'Never') return false
  if (Visibility === 'IfSignal') {
    return Connected === 'True' && SignalState === 'OK'
  }

  return true
}

// InputSourceType to icon name:
const icons = {
  pc: 'laptop',
  whiteboard: 'whiteboard',
  document_camera: 'document-camera',
  camera: 'camera',
}

const ShareTray = (props) => {
  const { status, commands } = props.appState.device
  const { share, call } = status

  const { connectors = [], presentation } = share
  const filtered = connectors.filter(showInShareTray)
  const connected = filtered.filter(i => i.Connected === 'True' && i.SignalState === 'OK')

  // we preselect any active source
  const active = presentation?.LocalInstance?.[0]?.Source
  let preselect = filtered[0]?.SourceId

  if (active) {
    preselect = active
  }
  else if (connected.length) {
    preselect = connected[0].SourceId
  }

  const [selected, setSelected] = useState(preselect)


  const items = filtered.map(con => {
    const { SourceId, Type, Connected, SignalState, Name } = con
    const connected = Connected === 'True' && SignalState === 'OK'

    // TODO: This may not be true. make a proper mapping function based on
    // [xStatus Video Input Source[n] ConnectorId]
    const connectorId = SourceId

    const inCall = !!call?.activeCall

    const shareState = presentation?.LocalInstance?.find(i => String(i.Source) === String(SourceId))
    const mode = shareState?.SendingMode
    const instanceId = shareState?.id

    const onView = connected && !mode
      && (() => commands.startPresentation(connectorId, 'LocalOnly'))

    const onShare = connected && inCall && mode !== 'LocalRemote'
      && (() => commands.startPresentation(connectorId, 'LocalRemote'))

    const onHide = mode === 'LocalOnly'
      && (() => commands.stopPresentation(instanceId))

    const onStop = mode === 'LocalRemote'
      && (() => commands.stopPresentation(instanceId))

    const isSelected = selected === SourceId

    let info = ''
    if (!connected) {
      info = `Connect the ${Type} to your device`
    }
    else if (mode === 'LocalOnly') {
      info = 'Viewing'
    }
    else if (mode === 'LocalRemote') {
      info = 'Sharing'
    }
    else {
      info = 'Connected'
    }

    const type = con.InputSourceType?.toLowerCase()
    const icon = icons[type] || 'input'

    return (
      <ShareTrayItem
        key={SourceId}
        name={Name}
        selected={isSelected}
        onView={onView}
        onShare={onShare}
        onHide={onHide}
        onStop={onStop}
        icon={icon}
        info={info}
        onSelect={() => setSelected(SourceId)}
      />
    )
  })

  if (share.code) {
    const info = `Use the Webex app or use this code on share.webex.com:\n${share.code}`
    const id = 'webex-share'
    items.push(<ShareTrayItem
      key={id}
      name="Webex sharing"
      info={info}
      logo="webex"
      selected={selected === id}
      onSelect={() => setSelected(id)}
    />)
  }

  return (
    <ShareTrayView items={items} />
  )
}

export default ShareTray
