import { useState, useEffect } from 'react'

import { getLog } from '../../log'

const LogViewer = () => {

  const [logs, setLogs] = useState(getLog())
  const [time, setTime] = useState(new Date())
  useEffect(() => {
    const timer = setInterval(() => {
      setLogs([...getLog()])
      setTime(new Date())
    }, 1_000)
    return () => clearInterval(timer)
  }, [])

  const items = logs.map(i => {
    const { time, type, message } = i
    const t = new Date(time).toLocaleTimeString('no')
    const msg = type === 'error' ? message.toUpperCase() : message
    return `${t}  ${msg}`
  }).join('\n')

  return (
    <div>
      <h2>Logs</h2>
      <div>{time.toLocaleTimeString()}</div>
      <textarea className="logs" value={items} readOnly />
    </div>
  )
}

export default LogViewer