import { useEffect, useState } from 'react'

import { VList as List } from '../../momentum'
import CallListItem from './CallListItem'

interface Props {
  searchText: string
  search: Function
  showDialog: Function
}

interface Person {
  ContactId: string
  Name: string
  Email: string
  Type: string
}

let debounceTimer = 0
const minTime = 300

function debounce(callback: Function) {
  clearTimeout(debounceTimer)
  debounceTimer = setTimeout(callback, minTime) as unknown as number
}


const DirectorySearch = (props: Props) => {
  const { searchText, search, showDialog } = props

  const [result, setResult] = useState<Person[]>([])

  useEffect(() => {
    if (searchText) {
      debounce(() => {
        search(searchText).then((res: any) => setResult(res))
      })
    }
    else {
      setResult([])
    }
  }, [searchText, search])

  const style = {
    margin: '3em 0',
  }

  const items = result.map(item => {
    const { ContactId, Name, Email } = item
    const showCard = () => showDialog('contactCard', { name: Name, number: Email })

    return <CallListItem key={ContactId} name={Name} onClick={showCard} />
  })

  return (
    <List
      hAlign="left"
      gap={24}
      style={style}
      className="md-wide md-scrollable-vertical"
    >
      {items}
    </List>
  )
}

export default DirectorySearch

