import { BottomBar, ButtonCircle, Icon, Page, VList, TopBar, Text } from '../../momentum'
import CallControls from './CallControls'
import VolumeControls from '../VolumeControls'
import AuthenticationPrompt from './AuthenticationPrompt'

const InCall = (props) => {
  const { actions, appState, hideInCall } = props
  const { status, commands } = appState.device
  const { call, audio } = status
  const { activeCall, conferenceInfo } = call

  const title = activeCall?.DisplayName
  const number = activeCall?.CallbackNumber?.split(':').pop()
  const prompt = conferenceInfo?.AuthenticationRequest

  // TODO only use Capabilities, when api is public in Stable sw
  const { DTMF } = conferenceInfo?.Capabilities || {}
  const isMeeting = conferenceInfo?.Meeting === 'True' || DTMF === 'True'
  // const canReact = Reactions === 'Available'

  const capabilities = {
    keypad: isMeeting,
    reactions: false,
  }

  let content = null

  // None, HostPinOrGuest,props.appState.
  if (prompt && prompt !== 'None') {
    content = <AuthenticationPrompt status={status} commands={commands} />
  }
  else {
    content = (
      <>
        <TopBar>
          <Text type="title" title={number} aria-live="polite">{title || 'Call'}</Text>
        </TopBar>
        <VList>
          <CallControls
            commands={commands}
            status={status}
            actions={actions}
            capabilities={capabilities}
          />
        </VList>
        <BottomBar />
      </>
    )
  }

  return (
    <Page center>
      <ButtonCircle type="secondary" onClick={hideInCall} className="md-hide-call">
        <Icon name="arrow-down" weight="bold" />
      </ButtonCircle>
      {content}
      <VolumeControls
        level={audio.volume}
        change={up => commands.audioChange(up)}
      />
    </Page>
  )
}

export default InCall
