import initialState from './state'
import { Commands } from '../types'

const DialDelay = 500

// TODO move to helper file

function createBooking(n: number) {
  const start = new Date()
  start.setMinutes(0)
  const end = new Date()
  end.setMinutes(0)
  end.setHours(end.getHours() + 1)

  const number = `meeting-${n}`
  return (
    {
      Id: number,
      Title: 'Discuss how simulator can be used to improve application',
      MeetingPlatform: 'Webex',
      Organizer: {
        FirstName: "SomeoneWithAVeryLongNameUsedAsAnEmailAddree@acme.com"
      },
      Time: {
        StartTime: start,
        EndTime: end,
      },
      DialInfo: {
        Calls: {
          Call : [{ Number: number }],
        },
      },
    }
  )
}

function createBookings(count: number) {
  const list = []
  for (let n = 0; n < count; n++) {
    list.push(createBooking(n))
  }
  return list
}

function createSources(count: number) {
  const names = ['PC (USB-C', 'PC (HDMI)']
  const list = []
  for (let n = 0; n < count; n++) {
    list.push({
      SourceId: n,
      Name: names[n] || `Source ${n}`,
      Connected: 'True',
      SignalState: 'OK',
      InputSourceType: 'PC',
    })
  }

  return list
}

function getSettingsFromUrl() {
  const params = new URLSearchParams(window.location.search)
  const bookings = params.has('bookings') ? Number(params.get('bookings')) : 1
  const sources = params.has('sources') ? Number(params.get('sources')) : 2
  const name = params.get('name') || 'Simulator'
  const number = params.get('number') || 'simulator@cisco.com'
  const recents = params.get('recents') || 20

  return { name, number, bookings, sources, recents }
}

function playTone(frequency: number, volume: number) {
  const context = new AudioContext()

  const gain = context.createGain()
  gain.gain.value = volume

  const oscillator = context.createOscillator()
  oscillator.type = 'sine'
  oscillator.frequency.value = frequency
  oscillator.connect(gain).connect(context.destination)
  oscillator.start()

  setTimeout(function () {
      oscillator.stop()
  }, 130)
}

function sleep(ms: number) {
  return new Promise(res => setTimeout(res, ms))
}

class Simulator {

  status: any = initialState
  subscriber: Function

  constructor(onUpdate: Function) {
    this.subscriber = onUpdate
    this.init()
  }

  init() {
    const settings = getSettingsFromUrl()

    const s = this.status
    s.displayName = settings.name
    s.systemInfo = {
      ProductId: 'Simulator',
      Software: { DisplayName: 'fake-sw' },
    }
    s.number = settings.number

    s.audio.volume = 50
    s.bookings = createBookings(settings.bookings)
    s.share.connectors = createSources(settings.sources)
    s.share.code = 'ABC-DEF-GHI'
  }

  async startIncomingCall(number: string) {
    await sleep(1000)
    this.status.call.incomingCall = {
      DisplayName: number,
    }
    this.notify()
  }

  async setCall(number: string, isMeeting: boolean = false) {
    await sleep(DialDelay)
    if (number === 'callback') {
      this.startIncomingCall('Stevie Wonder')
      return
    }

    this.status.call.activeCall = {
      DisplayName: number,
    }

    const prompt = number.match(/auth/i)
    this.status.call.conferenceInfo = {
      Meeting: isMeeting ? 'True' : 'False',
      AuthenticationRequest: prompt ? 'HostPinOrGuest' : 'None'
    }

    this.status.call.layouts = ['Stack', 'Prominent', 'Equal']
    this.status.call.currentLayout = 'Prominent'
    this.notify()
  }

  commands: Commands = {

    audioChange: (increment: boolean) => {
      const vol = this.status.audio.volume + (increment ? 5 : -5)
      const volLimited = Math.max(0, Math.min(100, vol))
      this.status.audio.volume = volLimited
      // playTone(600, volLimited / 100)
      this.notify()
    },

    dial: async (number: string) => {
      this.setCall(number)
      return true
    },

    endCall: async (callId?: string) => {
      this.status.call.activeCall = null
      this.status.call.layouts = []
      this.status.call.conferenceInfo = null
      this.status.call.pinError = false
      this.notify()
    },

    getBookings: async () => {
      return this.status.bookings
    },

    phonebookSearch: async (word: string) => {
      const hits = Math.max(0, 20 - word.length)
      const res = []
      for (let n = hits; n > 0; n--) {
        res.push({
          ContactId: n,
          Name: `Person ${n}`,
          Email: `person${n}@example.cisco.com`,
          Type: 'person',
        })
      }

      return res
    },

    getCallHistory: async () => {
      const list = []
      const count = Number(getSettingsFromUrl().recents)
      for (let n = 1; n <= count; n++) {
        const type = n < 4 ? 'Missed' : (n === 5 ? 'Rejected' : 'Placed')
        const time = Date.now() - 3800 * 1000 * n
        const dir = type === 'Missed' ? 'Incoming' : 'Outgoing'
        const name = n === 5
          ? 'Ridiculously long meeting titles should definitely be truncated so it doesn\'t overflow'
          : `Contact Number ${n}`

        list.push({
          id: n,
          DisplayName: name,
          LastOccurrenceStartTimeUTC: time,
          Direction: dir,
          CallbackNumber: `sip:contact${n}@acme.com`,
          OccurrenceType: type
        })
      }
      return list
    },

    getSystemInfo: async () => {},

    setMute: async (muted: boolean) => {
      this.status.audio.muted = muted
      this.notify()
    },

    setVideoMute: async (muted: boolean) => {
      this.status.video.muted = muted
      this.notify()
    },

    joinMeeting: async (booking: any) => {
      const number = booking.DialInfo?.Calls?.Call?.[0]?.Number
      this.setCall(number, true)
    },

    joinWebex: async (number: string) => {
      this.setCall(number, true)
    },

    joinZoom: async (number: string) => {
      this.setCall(number, true)
    },

    authResponse: async (ParticipantRole: string, Pin: string) => {
      const pin = Number(Pin?.replace('#', ''))

      if (pin === 0) {
        this.status.call.pinError = true
      }
      else {
        this.status.call.conferenceInfo.AuthenticationRequest = 'None'
      }
      this.notify()
    },

    acceptCall: async () => {
      this.setCall(this.status.call.incomingCall.DisplayName)
      this.status.call.incomingCall = null
      this.notify()
    },

    rejectCall: async () => {
      this.status.call.incomingCall = null
      this.notify()
    },

    sendDTMF: async (dtmf: string) => {
      const frequency = 400 + Number(dtmf) * 50 || 0
      playTone(frequency, 0.6)
    },

    setLayout: async (name: string) => {
      this.status.call.currentLayout = name
      this.notify()
    },

    setSelfView: async (on: boolean, position: string, fullscreen: boolean) => {
      this.status.selfView.on = on
      if (position) {
        this.status.selfView.position = position
      }
      if (typeof fullscreen === 'boolean') {
        this.status.selfView.fullScreen = fullscreen
      }
      this.notify()
    },

    startPresentation: async (ConnectorId: number, SendingMode: string ) => {
      this.status.share.presentation = {
        LocalInstance: [
          { Source: ConnectorId, SendingMode },
        ]
      }

      this.notify()
    },

    stopPresentation: async () => {
      this.status.share.presentation = null
      this.notify()
    },

    async showNotification(text: string, duration = 5) {},

    sendReaction: async (reaction: string) => {},

    setStandby: async (mode: 'standby' | 'halfwake' | 'off') => {},
  }

  notify() {
    if (this.subscriber) {
      this.subscriber(this.status)
    }
  }

}

export default Simulator


