import { useState, useEffect } from 'react'

import {
  ButtonPill, Checkbox, Icon, Page, VList, HList, TopBar, Text, TextInput,
} from '../../momentum'


const JoinZoom = (props: any) => {

  const { appState, closePage } = props
  const { device } = appState

  const [number, setNumber] = useState('')
  const [password, setPassword] = useState('')
  const [isHost, setIsHost] = useState(false)
  const [hostKey, setHostKey] = useState('')

  useEffect(() => {
    const lastNumber = localStorage.getItem('lastZoomNumber')
    if (lastNumber) {
      setNumber(lastNumber)
    }
  }, [])

  const setAndRememberNumber = (number: string) => {
    localStorage.setItem('lastZoomNumber', number)
    setNumber(number)
  }

  const join = () => {
    const fixed = number.replaceAll(' ', '').replaceAll('-', '')

    device.commands.joinZoom(fixed, password, isHost ? hostKey : null)
    if (closePage) {
      closePage()
    }
  }

  const validForm = number.length

  return (
    <Page onClose={closePage}>
      <TopBar>
        <HList gap={12}>
          <Icon className="zoom" size={24} />
          <Text type="title">Join Zoom</Text>
        </HList>
      </TopBar>
      <VList gap={48}>
        <TextInput
          placeholder="Enter meeting information"
          type="email"
          value={number}
          autoFocus
          onChange={n => setAndRememberNumber(n)}
          onSubmit={join}
        />
        <TextInput
          placeholder="Passcode"
          type="password"
          value={password}
          onChange={v => setPassword(v)}
          onSubmit={join}
        />
        { /** improve form handling */}
        <HList hAlign="space-between" style={{ width: 378 }}>
          <Text>I'm the host</Text>
          <Checkbox value={isHost} onChange={v => setIsHost(v)} />
        </HList>
        <TextInput
          placeholder="Host key"
          type="password"
          value={hostKey}
          onChange={v => setHostKey(v)}
          onSubmit={join}
          disabled={!isHost}
        />
        <ButtonPill
          onClick={join}
          disabled={!validForm}
          color="join"
        >Join</ButtonPill>
        <Text style={{ maxWidth: 600 }}>
          You can find the meeting information in your email invite.
        </Text>
      </VList>
    </Page>
  )
}

export default JoinZoom
