/**
 * Connects to a cisco device using local device user
 *
 * All the hairy dirty stuff is in here.
 *
 * Requires hostanem, username and password
 * Uses jsxapi websocket, monitors the connection with heartbeats and auto-reconnects
 */

import { Buffer } from 'buffer'
import DeviceModel from './DeviceModel'
import { log, warn, error } from '../log'
import Simulator from './Simulator'
import { Device, DeviceCreds, Status, Commands, Xapi} from '../types'

// required for jsxapi to work with app
window.Buffer = Buffer

const jsxapi = require('jsxapi')

const WaitBeforeReconnect = 3_000
const LocalStorageKey = 'creds'

function getCredsFromLocalStorage() {
  const item = localStorage.getItem(LocalStorageKey) || ''
  try {
    const creds = JSON.parse(item)
    return creds
  }
  catch(e) {
    log('no valid creds found')
  }

  return false
}

function getCredsFromUserParams() {
  const params = new URLSearchParams(window.location.search)
  if (params.has('host') && params.has('username')) {
    return {
      host: params.get('host'),
      username: params.get('username'),
      password: params.get('password') || '',
    }
  }
}

function isSim(host: string) {
  const h = host?.toLowerCase()
  return host === 'simulator' || h === 'sim'
}

class LocalConnector {

  xapi: Xapi | null = null
  subscriber: Function
  lastConnectStartTime = 0

  device: Device = {
    // notconnected: user has never connected succesfully, or has chosen to disconnect
    // connecting: user is manually connecting from login page
    // reconnecting: system is trying to auto-reconnect after having lost connection
    // connected:  app is connected to device
    // lostconnection: system lost connection (will typically start reconnecting quickly)
    connection: 'notconnected',
    creds: {
      host: '',
      username: '',
      password: '',
    },
    status: {} as Status,
    commands: {} as Commands,
    error: null,
    connectCount: 0,
  }
  deviceModel: any = null
  heartBeatTimer = 0

  constructor(subscriber: Function) {
    this.subscriber = subscriber
    this.notify()
  }

  autoConnect() {
    const creds = getCredsFromUserParams() || getCredsFromLocalStorage()
    if (creds) {
      this.connect(creds, true)
    }
  }

  notify() {
    this.subscriber(this.device)
  }

  connect(creds: DeviceCreds, isReconnect: boolean) {

    // close any previous web socket:
    if (this.xapi) {
      this.xapi.close()
    }

    const { host, username, password } = creds
    log((isReconnect ? 'Re-connecting to' : 'Connecting to'), host, username, '***')

    if (!host || !username) return

    this.device.error = null
    this.device.connectCount += 1
    this.device.connection = isReconnect ? 'reconnecting' : 'connecting'
    this.device.creds = creds
    this.notify()

    if (isSim(host)) {
      setTimeout(() => this.onConnected(null), 1000)
      return
    }


    // possible to enforce insecure ws by specifying ws:// in host
    // requires xconfiguration NetworkServices HTTP Mode: HTTP+HTTPS
    const path = host.startsWith('ws') ? host : 'wss://' + host

      jsxapi.connect(path, { username, password })
      .on('ready', (xapi: Xapi) => {

        // if user chose to cancel login, we just ignore the succesful login
        if (this.device.connection === 'notconnected' || this.device.connection === 'connected') {
          return
        }

        log('Logged in')

        if (!getCredsFromUserParams()) {
          localStorage.setItem(LocalStorageKey, JSON.stringify(creds))
        }

        this.onConnected(xapi)
      })
      .on('error', (e: any) => this.onError(e))

  }

  reconnect() {
    const c = this.device.connection
    if (c === 'connected' || c === 'connecting' || c === 'reconnecting') {
      return
    }
    this.connect(this.device.creds, true)
  }

  onConnected(xapi: Xapi | null) {
    this.xapi = xapi
    const useSimulator = isSim(this.device.creds.host)
    this.deviceModel = useSimulator
      ? new Simulator((status: Status) => this.onStatusUpdate(status))
      : new DeviceModel(xapi as Xapi, (status: Status) => this.onStatusUpdate(status))

    this.device.commands = this.deviceModel.commands
    this.device.status = this.deviceModel.status
    this.device.connection = 'connected'
    this.notify()
  }

  onError(e: any) {
    const previous = this.device.connection
    this.stopHeartBeat()

    // ignore any error occuring directly after another
    const now = Date.now()
    const timeSinceLast = now - this.lastConnectStartTime
    if (timeSinceLast < 500) {
      log('ignore second error')
      return
    }
    this.lastConnectStartTime = now

    if (previous === 'connected') {
      warn('Connection dropped, trying again')
      this.device.connection = 'lostconnection'
      this.device.error = 'Asleep'
      setTimeout(() => {
        this.reconnect()
      }, WaitBeforeReconnect)
    }
    else if (previous === 'lostconnection' || previous === 'reconnecting') {
      error('Didnt manage to reconnect', e)
      this.device.connection = 'lostconnection'
      this.device.error = 'Lost connection'
    }
    else if (previous === 'connecting') {
      error('Not able to log in')
      this.device.connection = 'notconnected'
      this.device.error = 'Not able to log in - see tips below.'
    }
    else if (previous === 'notconnected') {
      // dont need to flag error. was already disconnected (eg user cancelled)
    }

    this.notify()
  }

  onStatusUpdate(status: Status) {
    this.device.status = status
    this.notify()
  }

  stopHeartBeat() {
    clearInterval(this.heartBeatTimer)
  }

  disconnect() {
    log('Disconnecting')
    this.stopHeartBeat()
    this.device.error = null
    if (this.xapi) {
      this.xapi.close()
    }
    this.device.connection = 'notconnected'
    this.notify()
  }

}

export default LocalConnector
