import ReactDOM from 'react-dom/client'
import App from './App'

function loadApp(config) {
  const root = ReactDOM.createRoot(document.getElementById('root'))
  root.render(
    <App config={config} />
  )
}

async function fetchConfigAndLoad() {
  let cfg = {}
  try {
    const res = await fetch('./config.json')
    cfg = await res.json()
  }
  catch {}

  loadApp(cfg)
}

fetchConfigAndLoad()

