import { ButtonCircle, Icon, Text } from '..'
import { Props as BaseProps } from './ButtonPill'

interface Props extends BaseProps {
  name: string
  icon?: string
  description?: string
  logo?: string
  iconColor?: string
  weight?: string
}

const ButtonApp = (props: Props) => {
  const { id, icon, name, description, onClick, logo, type, color, iconColor, weight = 'filled' } = props
  const cls = 'md-button-app'
  const style = { backgroundColor: color }

  const iconOrLogo = logo
    ? <Icon className={logo} size={48} />
    : <Icon name={icon} color={iconColor} size={48} weight={weight} />

  return (
    <div className={cls}>
      <ButtonCircle
        id={id}
        size={110}
        onClick={onClick}
        type={type}
        style={style}
        title={description || name}
      >
        {iconOrLogo}
      </ButtonCircle>
      <Text aria-hidden>{name}</Text>
    </div>
  )
}

export default ButtonApp