import { useState } from 'react'

import { HList, VList as List, TopBar, BottomBar, Page, Text, TextInput, ButtonPill }from '../../momentum'

// https://roomos.cisco.com/xapi/Status.Conference.Call[n].AuthenticationRequest/?search=authenticationre&Type=Event:
//
// to test webinars, schedule and start a meeting from co.webex.com
//
// None
// The device is not waiting for an authentication response (no authentication request).

// HostPinOrGuest
// Participants are asked whether they want to join as a host or guest. They must provide the corresponding host PIN, or join as a guest without PIN.

// HostPinOrGuestPin
// Participants are asked whether they want to join as a host or guest. They must provide the corresponding host PIN or guest PIN.

// NOTINUSE: AnyHostPinOrGuestPin
// Participants are not asked if they are a host or guest; the role is not required. They must provide either a host PIN or guest PIN.

// PanelistPin
// Participants must provide a panelist PIN for joining a Webex Webinar as panelist. Joining as attendee is not supported for this webinar.

// PanelistPinOrAttendeePin
// Participants are asked whether they want to join a Webex Webinar as panelist or attendee. They must provide the corresponding panelist PIN or attendee PIN.

// PanelistPinOrAttendee
// GuestPin	Participants must provide a guest PIN.

const Authentication = (props) => {
  const { status, commands } = props
  const { conferenceInfo, pinError } = status.call

  const [askPin, setAskPin] = useState(false)
  const [pin, setPin] = useState('')
  const [role, setRole] = useState('')

  const prompt = conferenceInfo?.AuthenticationRequest
  let header = null
  let options = null

  const join = (role, pin) => {
    const pinWithHash = pin && !pin.endsWith('#') ? pin + '#' : pin
    commands.authResponse(role, pinWithHash)
  }
  const exit = () => commands.endCall()

  const fixedPill = { width: 200 }

  const errorMsg = pinError ? <Text type="error">Wrong PIN, please try again.</Text> : null

  if (askPin) {
    header = 'Please enter PIN code'
    options = (
      <HList>
        <TextInput
          placeholder="PIN"
          value={pin}
          onChange={v => setPin(v)}
          type="password"
          inputMode="numeric"
          onSubmit={() => join(role, pin)}
        />
        <ButtonPill style={fixedPill} onClick={() => join(role, pin)}>Go</ButtonPill>
      </HList>
    )
  }
  else if (prompt === 'HostPinOrGuest') {
    header = 'Are you the host?'
    const asHost = () => {
      setRole('Host')
      setAskPin(true)
    }
    options = (
      <HList>
        <ButtonPill type="secondary" style={fixedPill} onClick={asHost}>Yes</ButtonPill>
        <ButtonPill type="secondary" style={fixedPill} onClick={() => join('Guest')}>No</ButtonPill>
      </HList>
    )
  }
  else if (prompt === 'HostPinOrGuestPin') {
    header = 'How will you join?'
    const asHost = () => {
      setRole('Host')
      setAskPin(true)
    }
    const asGuest = () => {
      setRole('Guest')
      setAskPin(true)
    }
    options = (
      <HList>
        <ButtonPill type="secondary" style={fixedPill} onClick={asHost}>As Host</ButtonPill>
        <ButtonPill type="secondary" style={fixedPill} onClick={asGuest}>As Guest</ButtonPill>
      </HList>
    )
  }
  else if (prompt === 'PanelistPinOrAttendeePin' || prompt === 'PanelistPinOrAttendee') {
    header = 'Are you joining as a panelist or as an attendee?'
    const asPanelist = () => {
      setRole('Panelist')
      setAskPin(true)
    }
    const asGuest = () => {
      setRole('Guest')
      setAskPin(prompt === 'PanelistPinOrAttendeePin')
    }
    options = (
      <HList>
        <ButtonPill type="secondary" style={fixedPill} onClick={asPanelist}>Panelist</ButtonPill>
        <ButtonPill type="secondary" style={fixedPill} onClick={asGuest}>Attendee</ButtonPill>
      </HList>
    )
  }
  else {
    header = 'This controller does not know how to join meeting of type ' + prompt
    console.warn('Unknown prompt', prompt)
  }

  return (
    <Page center onClose={exit}>
      <TopBar>
        <Text type="title">{header}</Text>
      </TopBar>
      <List>
        {options}
        {errorMsg}
      </List>
      <BottomBar />
    </Page>
  )
}

export default Authentication
