import { useRef } from 'react'

import Icon from './Icon'

type InputMode = "text" | "search" | "email" | "tel" | "url" | "none" | "numeric" | "decimal" | undefined

interface Props {
  id?: string
  value?: string
  onChange: (text: string) => void
  placeholder?: string
  disabled?: boolean
  type?: string
  autoFocus?: boolean
  onSubmit?: Function
  autoCapitalize?: string
  inputMode?: InputMode
}

const TextInput = (props: Props) => {
  const {
    id,
    value,
    onChange,
    placeholder,
    disabled,
    type = 'text',
    autoFocus,
    onSubmit,
    autoCapitalize,
    inputMode,
  } = props
  
  const ref = useRef<HTMLInputElement>(null)

  const erase = () => {
    onChange('')
    setTimeout(() => {
      if (ref.current) {
        ref.current.focus()
      }
    }, 100)
  }

  const clearButton = value?.length ? (
    <button className="md-erase" onPointerDown={erase} tabIndex={-1}>
      <Icon name="clear" size={32} />
    </button>
  ) : null

  const onKeyDown = (e: any) => {
    const { key } = e
    if (onSubmit && (key === 'Enter' || key === 'Return')) {
      onSubmit()
    }
  }

  return (
    <div className="md-text-input" data-disabled={!!disabled}>
      <input
        id={id}
        value={value}
        onKeyDown={onKeyDown}
        onChange={e => onChange(e.target.value)}
        type={type}
        ref={ref}
        autoFocus={autoFocus}
        inputMode={inputMode}
        autoCapitalize={autoCapitalize}
        disabled={disabled}
        autoComplete="one-time-code"
        placeholder={placeholder}
      />
      {clearButton}
    </div>
  )
}

export default TextInput
