import { Component } from 'react'

import ErrorHandler from './components/ErrorHandler'
import ErrorPage from './components/ErrorPage'
import MainUI from './components/MainUI'
import Codec from './codec/LocalConnector'
import SwUpgrade from './components/SwUpgrade'
import { log, getLog } from './log'
import AppExtras from './components/AppExtras'
import { Momentum } from './momentum'

class App extends Component  {

  codec = null
  actions = {}

  constructor(props) {
    super(props)
    const { config = {} } = props

    this.state = {
      currentDialog: null,
      dialogData: null,
      device: null,
      config,
    }

    this.actions = {
      showDialog: this.showDialog.bind(this),
      closeDialog: this.closeDialog.bind(this),
      connect: this.connect.bind(this),
      reconnect: this.reconnect.bind(this),
      disconnect: this.disconnect.bind(this),
      resetConnectionError: this.resetConnectionError.bind(this),
    }
  }

  componentDidMount() {
    log('App started')
    this.codec = new Codec(device => this.onDeviceUpdated(device))
    this.codec.autoConnect()

    // for dev purposes, makes app available in dev console
    window.app = this
    window.getLog = getLog
    window.fakeConnectionLoss = () => this.codec.onError({ message: 'Fake connection loss' })

    window.onblur = () => log('document blur')
    document.onvisibilitychange = () => log('visibility:', document.visibilityState)
    window.onoffline = () => log('offline')
    window.ononline = () => log('online')

    let timestamp = Date.now()
    let sleep = false
    setInterval(() => {
      const now = Date.now()
      const elapsed = now - timestamp
      // console.log('elapsed', elapsed)
      timestamp = now
      if (elapsed > 300 && !sleep) {
        sleep = true
        log('went to sleep')
      }
      if (elapsed < 300 && sleep) {
        sleep = false
        log('awaken')
      }
    }, 200)
  }

  showDialog(dialog, dialogData) {
    this.setState({ currentDialog: dialog, dialogData })
  }

  closeDialog() {
    this.setState({ currentDialog: null, dialogData: null })
  }

  connect(creds) {
    this.codec.connect(creds)
  }

  onDeviceUpdated(device) {
    const { state } = this
    state.device = device
    this.setState(state)
  }

  reconnect() {
    this.codec.reconnect()
  }

  disconnect(wipeData = false) {
    this.codec.disconnect()
    this.showDialog(null)
    if (wipeData) {
      log('Wiping data')
      localStorage.clear()
      setTimeout(() => window.location.reload(), 500)
    }
  }

  resetConnectionError() {
    const { state } = this
    state.device.error = ''
    this.setState(state)
  }

  render() {
    const { actions, state } = this
    if (!state.device) return null

    // eslint-disable-next-line
    return (
      <Momentum>
        <ErrorHandler fallback={<ErrorPage />}>
          <MainUI actions={actions} appState={state} />
          <SwUpgrade standbyState={state.device?.status?.standbyState} />
          <AppExtras />
        </ErrorHandler>
      </Momentum>
    )
  }
}


export default App
