const items = []

let quiet = false

function add(type, ...args) {
  const message = args.join(' ')
  items.push({
    time: Date.now(),
    type,
    message,
  })

  // log to console too
  if (!quiet) {
    const t = type === 'log' ? 'debug' : type
    console[t](...args)
  }
}

export function silent(on) {
  quiet = on
}

export function log(...args) {
  add('log', ...args)
}

export function error(...args) {
  add('error', ...args)
}

export function warn(...args) {
  add('warn', ...args)
}

export function getLog() {
  return items
}
