import { Avatar, VList as List, Text, ButtonPill } from '../../momentum'

const ContactCard = (props: any) => {
  const { appState, actions } = props
  const { dialogData = {}, device } = appState
  const { name, number } = dialogData

  const num = number?.replace(/(.*):/, '')
  const numberIsReadable = num?.includes('@')
  const call = () => {
    device.commands.dial(number)
    actions.closeDialog()
  }

  return (
    <List gap={36}>
      <Avatar size={150} name={name} />
      <List>
        <Text type="title">{name}</Text>
        {numberIsReadable && <Text type="message">{num}</Text>}
      </List>
      <ButtonPill onClick={call} color="join">Call</ButtonPill>
    </List>
  )
}

export default ContactCard
