import classNames from 'classnames'

import { VList as List } from '../../momentum'
import CallListItem from './CallListItem'
import { formatTime } from '../../util'

interface CallHistoryItem {
  id: string
  DisplayName: string
  Direction: 'incoming' | 'outgoing'
  OccurrenceType: string
  CallbackNumber: string
  LastOccurrenceStartTimeUTC: string
  LastOccurrenceStartTime?: string // old api
}


const CallHistory = (props: any) => {
  const { list, showDialog } = props

  const items = list.map((item: CallHistoryItem) => {
    const {
      // id, Direction, DisplayName, IsAcknowledged, OccurrenceType, LastOccurrenceStartTimeUTC
      id, DisplayName, Direction, OccurrenceType, CallbackNumber, LastOccurrenceStartTimeUTC,
      LastOccurrenceStartTime,
    } = item

    // console.log(item)
    // const date = new Date(LastOccurrenceStartTimeUTC)
    // const day = date.toLocaleDateString()
    const t = LastOccurrenceStartTimeUTC || LastOccurrenceStartTime
    const time = formatTime(t, ['no']) // todo locale

    const status = OccurrenceType === 'NoAnswer' ? 'No Answer' : OccurrenceType
    const showCard = () => showDialog('contactCard', { name: DisplayName, number: CallbackNumber })

    const cls = classNames(
      'call-history-item md-wide',
      OccurrenceType?.toLowerCase(),
      Direction?.toLowerCase(),
    )

    const icon = Direction ? 'arrow-tail-up' : undefined

    return (
      <CallListItem
        key={id}
        name={DisplayName}
        className={cls}
        onClick={showCard}
        subText={`${status} ${time}`}
        icon={icon}
      />
    )
  })

  const style = {
    margin: '3em 0',
  }
  
  return (
    <List
      hAlign="left"
      gap={24}
      style={style}
      className="md-wide md-scrollable-vertical"
    >{items}</List>
  )
}

export default CallHistory
