const ConnectionStatus = (props) => {
  const { onCancel, error, onReconnect, connection } = props
  const isConnecting = connection === 'reconnecting'

  // const [showTips, setShowTips] = useState(false)

  const isAsleep = error === 'Asleep'

  let title
  let reconnectButton 
  let cancelButton = <ButtonPill type="ghost" onClick={onCancel}>Back to login</ButtonPill>

  if (isConnecting) {
    title = (
      <Text type="title">
        <Spinner /> Waking up
      </Text>
    )
  }
  else if (isAsleep) {
    title = <Text type="title">Controller is sleeping</Text>
    reconnectButton = <ButtonPill type="secondary" onClick={onReconnect}>Wake up</ButtonPill>
  }
  else {
    title = <Text type="title">Connection stopped</Text>
    reconnectButton = <ButtonPill type="secondary" onClick={onReconnect}>Reconnect</ButtonPill>
  }

  return (
    <ConnectionFade>
      <VList gap={48}>
        {title}
        {reconnectButton}
        {cancelButton}
      </VList>
    </ConnectionFade>
  )
}

export default ConnectionStatus