import { useState, useEffect } from 'react'

import { ButtonCircle, Icon, Text } from '../momentum'

const ShowLevelDuration = 2000
const PressAndHoldInterval = 250

const VolumeControls = (props: any) => {
  const { level, change } = props

  const down = () => change(false)
  const up = () => change(true)

  const [showLevel, setShowLevel] = useState(false)

  // show level briefly whenever it changes:
  useEffect(() => {
    setShowLevel(true)
    const timer = window.setTimeout(() => setShowLevel(false), ShowLevelDuration)
    return () => window.clearTimeout(timer)
  }, [level])

  const [timerId, setTimerId] = useState(0)

  const startLongPress = (increase: boolean) => {
    if (increase) {
      up()
    }
    else {
      down()
    }

    const id = setInterval(() => {
      if (increase) {
        up()
      }
      else {
        down()
      }
    }, PressAndHoldInterval)
    setTimerId(id as unknown as number)
  }

  const stopLongPress = () => {
    clearInterval(timerId)
  }

  const cls = 'indicator ' + (showLevel ? 'show' : '')

  return (
    <div className="volume-controls">
      <ButtonCircle
        id="volume-decrease"
        type="ghost"
        onPointerDown={() => startLongPress(false)}
        onPointerUp={stopLongPress}
        onPointerOut={stopLongPress}
        title="Decrease volume"
      >
        <Icon name="speaker-turn-down" size={28} weight="bold" />
      </ButtonCircle>
      <Text id="volume-status" className={cls} size={18}>{level}%</Text>
      <ButtonCircle
        id="volume-increase"
        type="ghost"
        onPointerDown={() => startLongPress(true)}
        onPointerUp={stopLongPress}
        onPointerOut={stopLongPress}
        title="Increase volume"
      >
        <Icon name="speaker-turn-up" size={28} weight="bold" />
      </ButtonCircle>
    </div>
  )
}

export default VolumeControls