import { useState, useEffect } from 'react'

import { ButtonPill, Page, VList as List, TopBar, Text, TextInput } from '../../momentum'
import CallHistory from './CallHistory'
import DirectorySearch from './DirectorySearch'


const StartACall = (props: any) => {

  const { appState, actions, closePage } = props
  const { device, config } = appState
  const [callHistory, setCallHistory] = useState([])
  const [number, setNumber] = useState('')
  const disabled = !number.length

  // on mount:
  useEffect(() => {
    if (!config.disableCallHistory) {
      device.commands.getCallHistory()
        .then((list: any) => setCallHistory(list))
    }
  }, [config, device.commands])

  const dial = () => {
    device.commands.dial(number.trim())
    if (closePage) {
      closePage()
    }
  }

  const historyList = !config.disableCallHistory
    && <CallHistory list={callHistory} showDialog={actions.showDialog} />

  const search = (word: string) => device.commands.phonebookSearch(word)
  const searchList = <DirectorySearch search={search} searchText={number} showDialog={actions.showDialog} />

  const list = number.length ? searchList : historyList

  return (
    <Page onClose={closePage}>
      <TopBar>
        <Text type="title">Call</Text>
      </TopBar>
      <List style={{ minHeight: 0 }}>
        <List gap={48}>
          <div style={{ display: 'flex', gap: 24 }}>
            <TextInput
              id="call-number"
              placeholder="Name, address or phone number"
              value={number}
              autoFocus
              onChange={n => setNumber(n)}
              onSubmit={dial}
            />
            <ButtonPill
              id="call-dial"
              onClick={dial}
              disabled={disabled}
              color="join"
            >Call</ButtonPill>
          </div>
          <Text style={{ maxWidth: 600 }}>
            You can find the meeting information in your calendar invite,
            eg 123456789 or username@example.webex.com.
          </Text>
        </List>
        {list}
      </List>

    </Page>
  )
}

export default StartACall
