/**
 * Maps xAPI state values and commmands to a local model
 *
 * Subscribes to xAPI status changes and maintains a local, synced model state
 * that the app can work with easily. Provide wrapper for xAPI commands
 *
 * Notifies app whenever there are changes, with an updated, full device model
 */

import { Commands as ICommands, Status as IStatus, StandbyState, Xapi } from '../types.js'
import Commands from './Commands'

import initialState from './state'

class DeviceModel {

  subscriber: Function
  status: IStatus = initialState

  xapi: Xapi
  commands: ICommands

  constructor(xapi: Xapi, subscriber: Function) {
    this.xapi = xapi
    this.commands = new Commands(xapi)
    this.subscriber = subscriber
    this.setupSync()
  }

  setupSync() {
    this.syncSystemInfo()
    this.syncCalls()
    this.syncTimeFormat()
    this.syncAudio()
    this.syncBookings()
    this.syncMute()
    this.syncVideoMute()
    this.syncShare()
    this.syncSelfView()
    this.syncLayouts()
    this.syncStandby()
  }

  syncShare() {
    this.getAndSubscribe('Webex Services Proximity GuestToken', (value: string) => {
      this.status.share.code = value
      this.notify()
    })

    this.getAndSubscribe('Video Input Connector', async () => {
      this.status.share.connectors = await this.xapi.Status.Video.Input.Connector.get()
      const configs = await this.xapi.Config.Video.Input.Connector.get()

      // add certain config values to the status list - needed for share tray
      configs.forEach((config: any, n: number) => {
        const con = this.status.share.connectors[n]
        if (con) {
          con.InputSourceType = config.InputSourceType
          con.Name = config.Name
          con.Visibility = config.Visibility
        }
      })

      this.notify()
    })

    this.getAndSubscribe('Conference Presentation', async () => {
      this.status.share.presentation = await this.xapi.Status.Conference.Presentation.get()
      this.notify()
    })

    this.getAndSubscribe('Conference Call', async () => {
      const v = await this.xapi.Status.Conference.Call.get()
      this.status.share.localWirelessShare = v?.[0]?.SessionType === 'Share'
      this.notify()
    })
  }

  async syncMute() {
    this.getAndSubscribe('Audio Microphones Mute', (value: string) => {
      this.status.audio.muted = value === 'On'
      this.notify()
    })
  }

  async syncVideoMute() {
    this.getAndSubscribe('Video Input MainVideoMute', (value: string) => {
      this.status.video.muted = value === 'On'
      this.notify()
    })
  }

  syncStandby() {
    this.getAndSubscribe('Standby State', (value: string) => {
      this.status.standbyState = value as StandbyState
      this.notify()
    })
  }

  syncCalls() {
    this.getAndSubscribe('Call', async () => {
      const calls = await this.xapi.Status.Call.get()

      const activeCall = calls.find((c: any) => c.Status === 'Connected'
        || (c.Status === 'Dialling')
        || c.Status === 'Connecting')

      this.status.call.activeCall = activeCall

      if (!activeCall) {
        this.status.call.pinError = false // reset after call
      }

      const incoming = calls.find((c: any) => c.Direction === 'Incoming' && c.Status === 'Ringing')
      this.status.call.incomingCall = incoming
      this.notify()
    })

    this.getAndSubscribe('Conference Call', async () => {
      const conferenceInfo = await this.xapi.Status.Conference.Call.get()
      // console.log('Request', conferenceInfo?.[0]?.AuthenticationRequest)
      this.status.call.conferenceInfo = conferenceInfo?.[0] || null
      this.notify()
    })

    this.xapi.Event.Conference.Call.AuthenticationResponse.on((e: any) => {
      console.log('auth response', e)
      this.status.call.pinError = !! e.PinError
      this.notify()
    })
  }

  syncLayouts() {
    this.getAndSubscribe('Video Layout CurrentLayouts', async () => {
      const list = await this.xapi.Status.Video.Layout.CurrentLayouts.AvailableLayouts.get()
      this.status.call.layouts = list.map ? list.map((i: any) => i.LayoutName) : []
      const current = await this.xapi.Status.Video.Layout.CurrentLayouts.ActiveLayout.get()
      this.status.call.currentLayout = current
      this.notify()
    })
  }

  syncSelfView() {
    this.getAndSubscribe('Video Selfview', async () => {
      const selfView = await this.xapi.Status.Video.Selfview.get()
      this.status.selfView = {
        on: selfView.Mode === 'On',
        fullScreen: selfView.FullscreenMode === 'On',
        position: selfView.PIPPosition,
      }
      this.notify()
    })
  }

  async syncSystemInfo() {
    const info = await this.xapi.Status.SystemUnit.get()
    const number = await this.xapi.Status.UserInterface.ContactInfo.ContactMethod[1].Number.get()
    this.status.displayName = info.BroadcastName || number
    this.status.systemInfo = info
    this.status.number = number
    this.notify()
  }

  syncAudio() {
    this.getAndSubscribe('Audio Volume', (value: string) => {
      this.status.audio.volume = Number(value)
      this.notify()
    })
  }

  syncTimeFormat() {
    this.getAndSubscribe('Time TimeFormat', (value : string) => {
      this.status.timeFormat = value as ('24H' | '12H')
      this.notify()
    }, 'config')
  }

  async syncBookings() {
    // update list anytime anything booking-related happens
    this.xapi.Event.Bookings.on(async () => {
      this.status.bookings = await this.commands.getBookings()
      this.notify()
    })
    this.status.bookings = await this.commands.getBookings()
    this.notify()
}

  notify() {
    this.subscriber(this.status)
  }

  async ping() {
    const maxPingTime = 3000
    const timeout = new Promise((res) => {
      setTimeout(() => res('timeout'), maxPingTime)
    })
    const getAudio = this.xapi.Status.Audio.Volume.get()

    try {
      const res = await Promise.race([getAudio, timeout])
      if (res === 'timeout') {
        console.log('timeout')
        return false
      }
      return true
    }
    catch {
      return false
    }
  }

  async getAndSubscribe(path: string, callback: Function, type: 'status' | 'config' = 'status') {
    try {
      await this.xapi[type].on(path, callback)
      await this.xapi[type].get(path).then(callback)
    }
    catch {
      console.warn('Not able to subscribe to', type, path)
      return null
    }
  }

}

export default DeviceModel
