import { VList, Text } from '../../momentum'

const Row = ({ name, value }) => {
  return (
    <VList hAlign='left' className="md-wide" gap={1}>
      <Text type="message">{name}</Text>
      <Text>{value}</Text>
    </VList>
  )
}

const DeviceInfo = (props) => {
  const { appState } = props
  const { creds, status } = appState.device

  return (
    <VList gap={48}>
      <Text type="title">About this device</Text>
      <VList vAlign="left" className="md-wide" gap={20}>
        <Row name="Display name" value={status.displayName} />
        <Row name="Model" value={status.systemInfo?.ProductId} />
        <Row name="Video address" value={status.number} />
        <Row name="IP address" value={creds?.host} />
        <Row name="Software" value={status.systemInfo?.Software?.DisplayName} />
      </VList>
    </VList>
  )
}

export default DeviceInfo
