import { useState } from 'react'

import {
  BottomBar,
  ButtonSimple,
  ButtonApp,
  HList,
  Icon,
  MeetingWidget,
  Page,
  TopBar,
} from '../../momentum'
import Wallpaper from '../Wallpaper'
import DashBoard from './DashBoard'
import StartACall from '../call/StartACall'
import HomeButtonGroup from './HomeButtonGroup'
import InCall from '../incall/InCall'
import VolumeControls from '../VolumeControls'
import JoinWebex from '../call/JoinWebex'
import JoinZoom from '../call/JoinZoom'
import BackToCallBar from './BackToCallBar'

const Home = (props) => {
  const { actions, appState } = props
  const { status, commands } = appState.device
  const { bookings, audio } = status

  const [pageName, setPageName] = useState(null)
  const [hideInCall, setHideIncall] = useState(false)

  const closePage = () => setPageName(null)

  const name = status?.displayName || 'Device info'
  const deviceName = name.split('@').shift()
  // const deviceCode  = appState.device.status?.share?.code

  const showDeviceInfo = () => actions.showDialog('aboutDevice')
  const showDial = () => setPageName('start-call')
  const showWebex = () => setPageName('join-webex')
  const showZoom = () => setPageName('join-zoom')
  const showCameraOptions = () => actions.showDialog('cameraControls')
  const showShare = () => actions.showDialog('shareTray')

  const inCall = status.call?.activeCall && !status.share.localWirelessShare

  let page
  if (inCall && !hideInCall) {
    page = <InCall appState={appState} actions={actions} hideInCall={() => setHideIncall(true) } />
  }
  else if (pageName === 'start-call') {
    page =  <StartACall {...props} closePage={closePage} />
  }
  else if (pageName === 'join-webex') {
    page = <JoinWebex {...props} closePage={closePage} />
  }
  else if (pageName === 'join-zoom') {
    page = <JoinZoom {...props} closePage={closePage} />
  }
  else if (pageName) {
    console.warn('Unknown page', pageName)
  }

  // TODO this should be defined elsewhere, and be configurable
  const buttonDef = [
    { icon: 'camera', id: 'call', name: 'Call', action: showDial },
    { logo: 'webex', id: 'webex', name: 'Webex', action: showWebex },
    { logo: 'zoom', id: 'zoom', name: 'Zoom', action: showZoom },
    { icon: 'share-screen', id: 'share', name: 'Share', action: showShare },
  ]

  const buttons = buttonDef.map(btn => (
    <ButtonApp
      key={btn.id}
      id={btn.id}
      icon={btn.icon}
      name={btn.name}
      logo={btn.logo}
      weight={btn.weight}
      onClick={btn.action}
    />
  ))

  const locale = status?.timeFormat === '24H' ? 'no' : 'us'
  const leftPage = (
    <MeetingWidget
      locale={locale}
      bookings={bookings}
      isInCall={inCall}
      join={b => commands.joinMeeting(b)}
    />
  )

  const rightPage = <HomeButtonGroup>{buttons}</HomeButtonGroup>

  return (
    <>
      <Wallpaper />
      <Page className="transparent" hidden={!!page}>
        <TopBar>
          <HList hAlign='space-between' className="md-wide">
            <ButtonSimple
              id="device-info-show"
              type="ghost"
              onClick={showDeviceInfo}
              title="Device info"
              oneLine
            >
              {deviceName}
              <Icon name="arrow-right" color="var(--mds-color-theme-text-secondary-normal)" />
            </ButtonSimple>
            <ButtonSimple id="settings-show" onClick={showCameraOptions} title="Settings">
              <Icon name="adjust-video" weight='light' size={36} />
            </ButtonSimple>
          </HList>
        </TopBar>
        <DashBoard left={leftPage} right={rightPage} />
        <BottomBar />
        {inCall && hideInCall &&
          <BackToCallBar showCall={() => setHideIncall(false)} />
        }
        <VolumeControls
          level={audio?.volume}
          change={up => commands.audioChange(up)}
        />
      </Page>
      {page}
    </>
  )
}

export default Home