import { useState, useEffect, ReactNode } from 'react'

import { HList, VList as List, Text, Checkbox } from '../../momentum'

const positions = [
  { key: 'UpperLeft', value: 'Upper left' },
  { key: 'UpperCenter', value: 'Upper Center' },
  { key: 'UpperRight', value: 'Upper right' },
  { key: 'CenterLeft', value: 'Center left' },
  { key: 'CenterCenter', value: '' },
  { key: 'CenterRight', value: 'Center right' },
  { key: 'LowerLeft', value: 'Lower left' },
  { key: 'LowerCenter', value: '' },
  { key: 'LowerRight', value: 'Lower right' },
]

interface PositionProps {
  onChange: Function
  value: string
  fullscreen: boolean
}

const PositionGrid = (props: PositionProps) => {
  const { onChange, value, fullscreen } = props
  const items = positions.map(p => {
    const select = () => onChange(p.key)
    const cls = 'pip-pos ' + (value === p.key ? 'selected' : '')
    if (!p.value) {
      return <div key={p.key} />
    }

    return <button key={p.key} title={p.value} className={cls} onClick={select}/>
  })

  if (fullscreen) {
    return (
      <div className="selfview-pip-grid fullscreen" />
    )
  }

  return (
    <div className="selfview-pip-grid">
      {items}
    </div>
  )
}

interface InputProps {
  id: string
  value: boolean
  onChange: (value: boolean) => any
  children: ReactNode
}

const Input = (props: InputProps) => {
  const { id, value, onChange, children } = props
  return (
    <HList>
      <Checkbox
        id={id}
        value={value}
        onChange={() => onChange(!value)}
      />
      <label htmlFor={id}>{children}</label>
    </HList>
  )
}

const SelfView = (props: any) => {
  const { device } = props.appState
  const { commands, status } = device
  const { on, position, fullScreen } = status.selfView

  const setPosition = (pos: string) => commands.setSelfView(true, pos)

  const [keepOn, setKeepOn] = useState(on)

  useEffect(() => {
    commands.setSelfView(true)
    return () => {
      if (!keepOn) {
        commands.setSelfView(false)
      }
    }
  }, [keepOn, commands])

  const setFullScreen = (full: boolean) => commands.setSelfView(true, undefined, full)

  return (
    <List>
      <Text type="title">Selfview</Text>
      <HList gap={48} vAlign='top' verticalWhenPortrait>
        <List>
          <Text type="message" size={16}>Visibility</Text>
          <Input
            id="keep-selfview-on"
            value={keepOn}
            onChange={v => setKeepOn(v)}
            >Show on screen permanently</Input>
        </List>
        <List>
          <Text type="message" size={16}>Position</Text>
          <PositionGrid
            value={position}
            fullscreen={fullScreen}
            onChange={setPosition}
          />
          <Input
            id="maximize-selfview"
            value={fullScreen}
            onChange={on => setFullScreen(on)}
          >Full screen</Input>
        </List>
      </HList>
    </List>
  )
}

export default SelfView
