import { HList, VList, Icon, Text, ButtonPill } from '..'

export function formatTime(fullTime, locale = []) {
  const format = { hour: '2-digit', minute:'2-digit' }
  return new Date(fullTime).toLocaleTimeString(locale, format);
}

const MeetingInfo = (props) => {

  const {
    title, organizer, platform, startTime, endTime, onJoin,
    locale, showJoin, number, canJoin,
  } = props

  const start = formatTime(startTime, locale)
  const end = formatTime(endTime, locale)

  const now = new Date()
  let status
  if (now < new Date(startTime)) {
    status = 'Meeting not started'
  }
  else if (now < new Date(endTime)) {
    status = 'Meeting in progress'
  }
  else {
    status = 'Meeting has ended'
  }

  const logo = platform?.toLowerCase()

  return (
    <section className="md-meeting-info">
      <VList hAlign="left" style={{ minWidth: 0 }}>
        <Text className="md-meeting-status" left>{status}</Text>
        <Text
          type="title"
          left
          oneLine
          title={title}
        >{title}</Text>
        <Text
          left
          oneLine
          title={organizer}
        >{`Organizer: ${organizer}`}</Text>
        <HList>
          {logo && <Icon className={logo} />}
          <Text left>{`${start} - ${end}`}</Text>
        </HList>
      </VList>
      <VList vAlign="center" hAlight="right" style={{ padding: '0 24px' }}>
        {showJoin &&
          <ButtonPill
            disabled={!canJoin}
            color="join"
            onClick={onJoin}
            title={`Join ${number}`}
          >Join</ButtonPill>
        }
      </VList>
    </section>
  )
}

export default MeetingInfo