import { ButtonCircle, Icon } from '..'

interface Props {
  id?: string
  onClick: Function
}

const CloseButton = (props: Props) => {
  const { id, onClick } = props

  // safari is not respecting the aspect-ratio:
  const style = { width: '6rem' }

  return (
    <ButtonCircle id={id} onClick={onClick} type="secondary" className="md-close" title="Close" style={style}>
      <Icon name="cancel" />
    </ButtonCircle>
  )
}

export default CloseButton