import { Avatar, Icon, VList as List, Text, HList } from '../../momentum'

interface ListItem {
  onClick: Function
  name: string
  subText?: string
  className?: string
  icon?: string
}

const CallItem = (props: ListItem) => {
  const { onClick, name, subText, className, icon } = props
  return (
    <HList
      gap={24}
      onClick={onClick}
      className={className}
      style={{ padding: '0 12px' }}
    >
      <Avatar name={name} />
      <List hAlign="left" gap={1} className="md-grow" style={{ minWidth: 0 }}>
        <Text oneLine style={{ minWidth: 0, maxWidth: 'min(600px, 70vw)' }} title={name}>{name}</Text>
        <Text type="message" size={18}>{subText}</Text>
      </List>
      {icon && <Icon name={icon} size={32} />}
    </HList>
  )
}

export default CallItem
