/**
 * General layout component for horizontal list.
 */
import { CSSProperties, ReactNode } from 'react'
import classNames from 'classnames'

interface Props {
  id?: string
  className?: string
  style?: CSSProperties
  children?: ReactNode
  gap?: number
  hAlign?: 'left' | 'right' | 'center' | 'space-between'
  vAlign?: 'top' | 'center' | 'bottom' | 'stretch'
  wrap?: boolean
  onClick?: Function
  verticalWhenPortrait?: boolean
}

const HList = (props: Props) => {
  const {
    id,
    className,
    style,
    children,
    gap,
    wrap,
    vAlign = 'center',
    hAlign = 'left',
    onClick,
    verticalWhenPortrait,
  } = props

  const cls = classNames('md-hlist', className, verticalWhenPortrait && 'vertical-in-portrait')

  const styles = {...style} || {} 
  if (gap) {
    styles.gap = (gap / 10) + 'rem'
  }
  if (wrap) {
    styles.flexWrap = 'wrap'
  }

  const justify = {
    left: 'flex-start',
    right: 'flex-end',
    center: 'center',
    'space-between': 'space-between',
  }
  const align = {
    top: 'flex-start',
    center: 'center',
    bottom: 'flex-end',
    stretch: 'stretch',
  }

  styles.justifyContent = justify[hAlign]
  styles.alignItems = align[vAlign]

  return (
    <div
      id={id}
      className={cls}
      style={styles}
      onClick={() => onClick && onClick()}
    >{children}</div>
  )
}

export default HList