import { ReactNode, CSSProperties } from 'react'

interface Props {
  id?: string
  children: ReactNode
  type?: 'title' | 'message' | 'error'
  title?: string
  className?: string
  style?: CSSProperties
  size?: number
  left?: boolean
  oneLine?: boolean
  multiLine?: boolean
  'aria-hidden'?: boolean
  'aria-live'?: 'off' | 'assertive' | 'polite'
}

const Text = (props: Props) => {
  const { id, type, children, className, style = {}, title, size, left, oneLine, multiLine } = props
  let cls = 'md-text ' + (className || '')

  if (size) {
    style.fontSize = (size / 10) + 'rem'
  }
  if (left) {
    style.textAlign = 'left'
  }
  if (multiLine) {
    style.lineHeight= '1.5em'
  }
  if (oneLine) {
    cls += ' md-one-line'
  }

  return (
    <div
      id={id}
      className={cls}
      style={style}
      title={title}
      data-type={type}
      aria-hidden={props['aria-hidden']}
      aria-live={props['aria-live']}
    >{children}</div>
  )
}

export default Text
