// Keep this page dependency free. want no ways for it to fail
const style = {
  justifyContent: 'center'
}

export default function ErrorPage() {
  const reload = () => window.location.reload()

  return (
    <div className="md-page" style={style}>
      <span style={{ fontSize: 80 }}>🫢</span>
      Looks like something went bad.
      <p />
      <button onClick={reload} className="md-button-pill">Reload application</button>
    </div>
  )
}