import { CSSProperties, useMemo } from 'react'

// TODO: we probably want to create stylesheet instead
import glyphs from '../fonts/glyphs.json'

interface Props {
  name?: string
  /** 'regular' | 'filled' | 'bold' | 'light' */
  weight?: string
  size?: number
  color?: string
  className?: string
}

const Icon = (props: Props) => {
  const { name, weight = 'regular', size = 20, color, className } = props

  const s = `${size / 10}rem`
  const style : CSSProperties = {
    fontSize: s,
    height: s,
    width: s,
    lineHeight: s,
  }

  if (color) {
    style.color = color
  }

  const key = `${name}-${weight}`

  const list = Object.values(glyphs)
  const glyph = list.find(g => g.name === key)

  const symbol = useMemo(() => {
    if (!name) return

    const str = glyph && String.fromCodePoint(glyph.codepoint)
    if (!str) {
      console.warn('No icon found:', name)
    }
    return str
  }, [name, glyph])

  const cls = 'md-icon ' + (className || '')

  return (
    <i className={cls} aria-hidden style={style}>{symbol}</i>
  )
}

export default Icon