import { useEffect } from 'react'
import { StandbyState } from '../types'
import { getLatestSwBuild } from '../util'

type SwBuild = {
  date: string
  hash: string
}

declare global {
  interface Window { build: SwBuild }  
}

interface Props {
  currentBuild: SwBuild
  standbyState: StandbyState
}

const SwUpgrade = (props: Props) => {
  const { standbyState } = props

  useEffect(() => {
    if (standbyState === 'Halfwake' || standbyState === 'Standby') {
      getLatestSwBuild()
        .then((newest: SwBuild | null) => {
          const current = window.build as SwBuild
          if (newest?.hash &&  current.hash !== newest.hash) {
            console.log('New sw detected. Reload app')
            window.location.reload()
          }
        })
    }
  }, [standbyState])

  return null
}

export default SwUpgrade
