import { log, error, warn } from '../log'
import { Commands as ICommands } from '../types'

interface Xapi {
  command: (path: string, options?: object | null) => any
  Status: any
}

class Commands implements ICommands {

  xapi: Xapi
  history: object[] = []

  constructor(xapi: any) {
    this.xapi = xapi
  }

  async command(path: string, options?: object | null) {
    try {
      log('xCommand ' + path, options)
      this.history.push({ path, options })
      this.history = this.history.slice(-1000)

      // when testing:
      if (!this.xapi) return false

      const res = await this.xapi.command(path, options) 
      return res || true
    }
    catch(e) {
      error(e)
      return false
    }
  }

  async getBookings() {
    const res = await this.xapi.command('Bookings List')
    const list = res?.Booking || []
    return list
  }

  async getCallHistory() {
    const res = await this.xapi.command('CallHistory Recents')
    return res?.Entry || []
  }

  async phonebookSearch(word: string) {
    const res = await this.command('Phonebook Search', { PhonebookType: 'Corporate', SearchString: word })
    return res?.Contact || []
  }

  getSystemInfo() {
    return this.xapi.Status.SystemUnit.get()
  }

  async dial(number: string) {
    return await this.command('Dial', { Number: number })
  }

  async setMute(muted: boolean) {
    const cmd = muted ? 'Audio Microphones Mute' : 'Audio Microphones Unmute'
    return await this.command(cmd)
  }

  async setVideoMute(muted: boolean) {
    const cmd = muted ? 'Video Input MainVideo Mute' : 'Video Input MainVideo Unmute'
    return await this.command(cmd)
  }

  async joinMeeting(booking: object) {
    // todo: support zoom, webrtc
    const { Id, DialInfo, MeetingPlatform } = booking as any
    const dialInfo = DialInfo?.Calls?.Call?.[0]
    const number = dialInfo?.Number

    if (MeetingPlatform === 'Webex') {
      return await this.command('Dial', { Number: number, BookingId: Id })
    }
    else if (MeetingPlatform === 'Zoom') {
      return await this.command('Zoom Join', { BookingID: Id })
    }
    else if (MeetingPlatform === 'GoogleMeet') {
      return await this.command('WebRTC Join' , { Type: 'GoogleMeet', BookingId: Id, Url: number  })
    }

    warn('Unsupported meeting platform:', MeetingPlatform)
    return false
  }

  async joinWebex(number: string) {
    return await this.command('Webex Join', { Number: number })
  }

  async joinZoom(number: string, password?: string, hostKey?: string) {
    const o: any = { MeetingID: number }
    if (password) {
      o.MeetingPasscode = password
    }
    if (hostKey) {
      o.HostKey = hostKey
    }

    return await this.command('Zoom Join', o)
  }

  async authResponse(ParticipantRole: string, Pin: string) {
    return await this.command('Conference Call AuthenticationResponse', { ParticipantRole, Pin })
  }

  async endCall(callId?: string) {
    const opts = callId ? { CallId: callId } : null
    return await this.command('Call Disconnect', opts)
  }

  async acceptCall() {
    return await this.command('Call Accept')
  }

  async rejectCall() {
    return await this.command('Call Reject')
  }

  async sendDTMF(dtmf: string) {
    return await this.command('Call DTMFSend', { DTMFString: String(dtmf) })
  }

  audioChange(increase: boolean, playSound = true) {
    if (increase) {
      this.command('Audio Volume Increase')
    }
    else {
      this.command('Audio Volume Decrease')
    }
    if (playSound) {
      this.command('Audio Sound Play', { Sound: 'VolumeAdjust' })
    }
  }

  async setLayout(name: string) {
    return await this.command('Video Layout SetLayout', { LayoutName: name })
  }

  async setSelfView(on: boolean, position: string, fullscreen: boolean) {
    const options: any = {}
    if (typeof on === 'boolean') {
      options.Mode = on ? 'On' : 'Off'
    }
    if (position) {
      options.PIPPosition = position
    }
    if (typeof fullscreen === 'boolean') {
      options.FullscreenMode = fullscreen ? 'On' : 'Off'
    }

    return await this.command('Video SelfView Set', options)
  }

  async startPresentation(ConnectorId: Number, SendingMode: String ) {
    const options = { ConnectorId, SendingMode }
    return await this.command('Presentation Start', options)
  }

  async stopPresentation(instance: number, presentationSource: number) {
    const options: any = {}
    if (instance) {
      options.Instance = instance
    }
    if (presentationSource) {
      options.PresentationSource = presentationSource
    }
    return await this.command('Presentation Stop', options)
  }

  setStandby(mode: 'standby' | 'halfwake' | 'off') {
    const cmds = {
      standby: 'Standby Activate',
      halfwake: 'Standby Halfwake',
      off: 'Standby Deactivate',
    }

    const cmd = cmds[mode]
    if (cmd) {
      return this.command(cmd)
    }
  }

  async showNotification(text: string, duration = 5) {
    const o = {
      Text: text,
      X: 5000,
      Y: 3000,
      Duration: duration,
    }
    return await this.command('UserInterface Message TextLine Display', o)
  }

  async sendReaction(reaction: string) {
    return await this.command('Conference Reaction Send', { ReactionType: reaction })
  }
}

export default Commands
