/**
 * General layout component for vertical list.
 */
import { CSSProperties, ReactNode } from 'react'
import classNames from 'classnames'

interface Props {
  id?: string
  className?: string
  style?: CSSProperties
  children?: ReactNode
  gap?: number
  hAlign?: 'stretch' | 'left' | 'right' | 'center'
  vAlign?: 'top' | 'center' | 'bottom' | 'space-between'
  onClick?: Function
}

const VList = (props: Props) => {
  const {
    id,
    className,
    style,
    children,
    gap,
    vAlign = 'top',
    hAlign = 'center',
    onClick
  } = props

  const justify = {
    top: 'flex-start',
    bottom: 'flex-end',
    center: 'center',
    'space-between': 'space-between',
  }
  const align = {
    left: 'flex-start',
    center: 'center',
    right: 'flex-end',
    stretch: 'stretch',
  }
  
  const cls = classNames(
    'md-vlist',
    hAlign && 'md-halign-' + hAlign,
    vAlign && 'md-valign-' + vAlign,
    className,
  )

  const styles = {...style} || {}
  styles.justifyContent = justify[vAlign]
  styles.alignItems = align[hAlign]

  if (gap) {
    styles.gap = (gap / 10) + 'rem'
  }

  return (
    <div
      id={id}
      className={cls}
      style={styles}
      onClick={() => onClick && onClick()}
    >{children}</div>
  )
}

export default VList