const keys = [
  { main: 1, letters: '' },
  { main: 2, letters: 'abc' },
  { main: 3, letters: 'def' },
  { main: 4, letters: 'ghi' },
  { main: 5, letters: 'jkl' },
  { main: 6, letters: 'mno' },
  { main: 7, letters: 'pqrs' },
  { main: 8, letters: 'tuv' },
  { main: 9, letters: 'wxyz' },
  { main: '*' },
  { main: 0, letters: '+' },
  { main: '#' },
]

interface Props {
  onClick: Function
}

interface ButtonProps {
  main: string | number
  letters?: string
  onClick: Function
}

const Button = (props: ButtonProps) => {
  const { main, letters, onClick } = props
  return (
    <button id={`keypad-${main}`} onClick={() => onClick(main)}>
      <div className="md-numeric">{main}</div>
      <label>{letters}</label>
    </button>
  )
}

const KeyPad = (props: Props) => {
  const { onClick } = props

  const buttons = keys.map((button) => {
    return <Button key={button.main} main={button.main} onClick={onClick} letters={button.letters} />
  })

  return (
    <div className="md-keypad">
      {buttons}
    </div>
  )
}

export default KeyPad