import { useEffect } from 'react'

const params = new URLSearchParams(window.location.search)

const AppExtras = () => {

  useEffect(() => {
    let filter = ''
    if (params.get('blur')) {
      filter = 'blur(10px)'
    }
    if (params.get('nocolor')) {
      filter += ' saturate(0)'
    }

    document.body.style.filter = filter

  })
}

export default AppExtras