import ConnectionDialog from './SetupConnection'
import HomeScreen from './home/HomeScreen'
import DialogSelector from './dialogs/DialogSelector'
import IncomingCall from './notifications/IncomingCall'
import ConnectionStatus from './notifications/ConnectionStatus'

import '../assets/styles/index.css'
import '../assets/styles/responsive.css'

const MainUI = (props) => {
  const { appState, actions } = props
  const { connection, error, status, commands } = appState.device

  const notConnected = connection === 'notconnected' || connection === 'connecting'

  // console.debug('app state', appState)

  let page = null;

  if (notConnected) {
    page = <ConnectionDialog appState={appState} actions={actions} />
  }

  let notification
  const incomingCall = status?.call?.incomingCall
  const showConnect = connection === 'lostconnection'
    || connection === 'reconnecting'

  const acceptCall = async () => {
    if (status.call.activeCall) {
      await commands.endCall(status.call.activeCall.id)
    }
    commands.acceptCall()
  }

  if (showConnect) {
    notification = (
      <ConnectionStatus
        error={error}
        connection={connection}
        onReconnect={actions.reconnect}
        onCancel={() => actions.disconnect(false)}
      />
    )
  }
  else if (incomingCall) {
    notification = (
      <IncomingCall
        caller={incomingCall.DisplayName}
        alreadyInCall={!!status.call.activeCall}
        onAnswer={acceptCall}
        onDecline={() => commands.rejectCall()}
      />
    )
  }
  const dialog = <DialogSelector {...props} />

  return (
    <main className="main-ui">
      <HomeScreen appState={appState} actions={actions} />
      {page}
      {dialog}
      {notification}
    </main>
  )
}

export default MainUI