const HomeButtonGroup = (props) => {

  // center button when only one
  const style = {}
  if (props.children?.length < 2) {
    style.gridTemplateColumns = '1fr'
  }

  return (
    <section className="home-button-group" style={style} aria-label="Main features">
      {props.children}
    </section>
  )
}

export default HomeButtonGroup