import { Avatar, VList, Text, HList, Icon, ButtonPill } from '../../momentum'
import Notification from './Notification'

const IncomingCall = (props) => {
  const { caller = 'Unknown caller', onAnswer, onDecline, alreadyInCall } = props
  const style = { fontSize: 18, width: 100  }

  const info = alreadyInCall && (
    <Text type="message">Accepting this call will end the current call.</Text>
  )

  return (
    <Notification>
      <VList>
        <HList className="md-wide">
          <Avatar>
            <Icon name="audio-call" weight="bold" />
          </Avatar>
          <VList hAlign="left" gap={5}>
            <Text>{caller}</Text>
            <Text type="success" size={18}>Incoming call</Text>
          </VList>
        </HList>
        <HList className="md-wide" hAlign="right">
          <ButtonPill
            size={40}
            color="join"
            style={style}
            onClick={onAnswer}
          >Answer</ButtonPill>
          <ButtonPill
            size={40}
            color="cancel"
            style={style}
            onClick={onDecline}
          >Decline</ButtonPill>
        </HList>
        {info}
      </VList>
    </Notification>
  )
}

export default IncomingCall
