interface Props {
  id?: string
  value: boolean
  onChange: (newValue: boolean) => any
}

const Checkbox = (props: Props) => {
  const { id, value, onChange } = props
  const cls = 'md-checkbox ' + (value ? 'md-selected' : '')

  return (
    <button id={id} className={cls} onClick={() => onChange(!value)} />
  )
}

export default Checkbox