import { useState, useEffect } from 'react'

import { ButtonPill, Icon, HList, Page, VList, TopBar, Text, TextInput } from '../../momentum'

const JoinWebex = (props: any) => {

  const { appState, closePage } = props
  const { device } = appState
  const [number, setNumber] = useState('')
  const disabled = !number.length

  useEffect(() => {
    const lastNumber = localStorage.getItem('lastMeetingNumber')
    if (lastNumber) {
      setNumber(lastNumber)
    }
  }, [])

  const setAndRememberNumber = (number: string) => {
    localStorage.setItem('lastMeetingNumber', number)
    setNumber(number)
  }

  const join = () => {
    const fixed = number.replaceAll(' ', '').replaceAll('-', '')
    device.commands.joinWebex(fixed)
    if (closePage) {
      closePage()
    }
  }

  return (
    <Page onClose={closePage}>
      <TopBar>
        <HList gap={12}>
          <Icon className="webex" size={24} />
          <Text type="title">Join Webex</Text>
        </HList>
      </TopBar>
      <VList gap={48}>
        <HList>
          <TextInput
            placeholder="Enter meeting information"
            type="email"
            autoFocus
            value={number}
            onChange={n => setAndRememberNumber(n)}
            onSubmit={join}
          />
          <ButtonPill
            onClick={join}
            disabled={disabled}
            color="join"
          >Join</ButtonPill>
        </HList>
        <Text style={{ maxWidth: 600 }}>
          You can find the meeting information in your calendar invite,
          eg 123456789 or username@example.webex.com.
        </Text>
      </VList>
    </Page>
  )
}

export default JoinWebex
