import CameraControls from '../cameracontrols/CameraControls'
import ContactCard from '../call/ContactCard'
import DeviceDialog from '../home/AboutDevice'
import Dialog from './Dialog'
import Keypad from './KeypadDialog'
import Layouts from './Layouts'
import LogViewer from './LogViewer'
import ShareTray from '../share/ShareTray'
import AboutDevice from '../home/DevicePopup'
import AboutController from '../home/AboutController'

const DialogSelector = (props) => {
  const { appState, actions } = props
  const { currentDialog } = appState

  let dialog = null
  let pos = null

  if (currentDialog === 'deviceInfo') {
    dialog = <DeviceDialog {...props} />
  }
  else if (currentDialog === 'controllerInfo') {
    dialog = <AboutController {...props} />
  }
  else if (currentDialog === 'logViewer') {
    dialog = <LogViewer />
  }
  else if (currentDialog === 'dtmf') {
    dialog = <Keypad {...props} />
  }
  else if (currentDialog === 'layouts') {
    dialog = <Layouts {...props} />
  }
  else if (currentDialog === 'cameraControls') {
    dialog = <CameraControls {...props} />
  }
  else if (currentDialog === 'aboutDevice') {
    dialog = <AboutDevice {...props} />
    pos = { position: 'absolute', top: '10rem', left: '2.4rem', borderRadius: 12 }
  }
  else if (currentDialog === 'shareTray') {
    dialog = <ShareTray {...props} />
  }
  else if (currentDialog === 'contactCard') {
    dialog = <ContactCard {...props} />
  }
  else if (currentDialog) {
    console.warn('Unknown dialog', currentDialog)
  }


  const close = () => actions.showDialog(null)

  return dialog && <Dialog close={close} pos={pos}>{dialog}</Dialog>
}

export default DialogSelector
